'use client';

import React, { useEffect, useState } from "react";
import Link from "next/link";
import axiosInstance from "@/utils/axiosInstance";
import { Package, ChevronRight, Sparkles, LayoutGrid, Frame, Monitor } from "lucide-react";

const OurCollection = () => {
    const [products, setProducts] = useState([]);
    const [loading, setLoading] = useState(true);
    const [activeFilter, setActiveFilter] = useState("all");

    useEffect(() => {
        const fetchAllProducts = async () => {
            try {
                const types = ['acrylic', 'canvas', 'backlight'];
                const promises = types.map(async (type) => {
                    // Optimized with cache-busting timestamp
                    const res = await axiosInstance.get(`frames/${type}?t=${Date.now()}`);
                    return res.data.map(item => ({ ...item, type }));
                });

                const results = await Promise.all(promises);
                const merged = results.flat();
                setProducts(merged);
                setLoading(false);
            } catch (error) {
                console.error("Failed to fetch products:", error);
                setProducts([]);
                setLoading(false);
            }
        };

        fetchAllProducts();
    }, []);

    const displayedProducts = activeFilter === "all"
        ? products
        : products.filter(p => p.type === activeFilter);

    const categories = [
        { id: "all", label: "All Frames", icon: <LayoutGrid className="w-4 h-4" /> },
        { id: "acrylic", label: "Acrylic", icon: <Sparkles className="w-4 h-4" /> },
        { id: "canvas", label: "Canvas", icon: <Frame className="w-4 h-4" /> },
        { id: "backlight", label: "Backlight", icon: <Monitor className="w-4 h-4" /> },
    ];

    return (
        <section className="py-24 bg-white font-[Poppins]">
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                {/* Header Section */}
                <div className="flex flex-col md:flex-row md:items-end justify-between mb-12 gap-6">
                    <div className="space-y-4">
                        <div className="inline-flex items-center gap-2 px-3 py-1 bg-primary/10 text-primary rounded-full text-xs font-bold uppercase tracking-wider">
                            <Package className="w-3 h-3" />
                            <span>Premium Selection</span>
                        </div>
                        <h2 className="text-4xl md:text-5xl font-extrabold text-secondary leading-tight">
                            Our <span className="text-primary italic">Signature</span> Collection
                        </h2>
                        <p className="text-neutral-500 text-lg max-w-xl">
                            Transform your favorite memories into museum-grade art with our curated selection of customizable frames.
                        </p>
                    </div>

                    <Link
                        href="/products"
                        className="group flex items-center gap-2 text-primary font-bold text-lg hover:translate-x-1 transition-transform"
                    >
                        View Full Collection
                        <ChevronRight className="w-5 h-5" />
                    </Link>
                </div>

                {/* Filter Tabs */}
                <div className="flex flex-wrap gap-3 mb-12">
                    {categories.map((cat) => (
                        <button
                            key={cat.id}
                            onClick={() => setActiveFilter(cat.id)}
                            className={`flex items-center gap-2 px-6 py-3 rounded-2xl font-bold text-sm transition-all duration-300 ${activeFilter === cat.id
                                ? "bg-secondary text-white shadow-xl shadow-secondary/20 scale-105"
                                : "bg-neutral-50 text-neutral-500 hover:bg-neutral-100"
                                }`}
                        >
                            {cat.icon}
                            {cat.label}
                        </button>
                    ))}
                </div>

                {/* Grid Section */}
                {loading ? (
                    <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-8">
                        {[...Array(4)].map((_, i) => (
                            <div key={i} className="aspect-square bg-neutral-100 animate-pulse rounded-3xl" />
                        ))}
                    </div>
                ) : (
                    <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-8">
                        {displayedProducts.map((product, index) => (
                            <Link
                                key={product.id || index}
                                href={`/shop/${product.type}/${product.shape.toLowerCase()}`}
                                className="group relative flex flex-col h-full bg-white rounded-3xl overflow-hidden border border-neutral-100 hover:shadow-2xl transition-all duration-500"
                            >
                                {/* Image Container */}
                                <div className="relative aspect-[4/5] overflow-hidden">
                                    <img
                                        src={product.image || "https://via.placeholder.com/400?text=No+Image"}
                                        alt={product.title}
                                        className="w-full h-full object-cover transition-transform duration-700 group-hover:scale-110"
                                    />
                                    <div className="absolute inset-0 bg-gradient-to-t from-black/60 via-transparent to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-500" />

                                    <div className="absolute top-4 left-4">
                                        <span className="px-3 py-1.5 bg-white/90 backdrop-blur-md rounded-full text-[10px] font-black uppercase text-secondary shadow-sm">
                                            {product.type}
                                        </span>
                                    </div>

                                    <div className="absolute bottom-6 left-6 right-6 translate-y-4 opacity-0 group-hover:translate-y-0 group-hover:opacity-100 transition-all duration-500">
                                        <button className="w-full py-3 bg-white text-secondary font-bold rounded-2xl text-sm shadow-xl hover:bg-primary hover:text-white transition-colors">
                                            Customize This
                                        </button>
                                    </div>
                                </div>

                                {/* Content */}
                                <div className="p-6">
                                    <div className="flex justify-between items-start mb-2">
                                        <h3 className="font-bold text-secondary text-lg group-hover:text-primary transition-colors line-clamp-1">
                                            {product.title}
                                        </h3>
                                    </div>
                                    <div className="flex items-center justify-between mt-auto">
                                        <div className="flex flex-col">
                                            <span className="text-[10px] text-neutral-400 font-bold uppercase tracking-wider">Starting at</span>
                                            <span className="text-xl font-black text-secondary group-hover:text-primary">
                                                ₹{product.sizes?.[0]?.price || 0}
                                            </span>
                                        </div>
                                        <div className="h-8 w-8 rounded-full border border-neutral-200 flex items-center justify-center group-hover:bg-primary group-hover:border-primary transition-all">
                                            <ChevronRight className="w-4 h-4 text-neutral-400 group-hover:text-white" />
                                        </div>
                                    </div>
                                </div>
                            </Link>
                        ))}
                    </div>
                )}
            </div>
        </section>
    );
};

export default OurCollection;
